<template>
    <div>
        1.(修饰符)i执行对大小写不敏感的匹配 g执行全局匹配（查找所有匹配而非在找到第一个匹配后停止） m执行多行匹配<br/>
        2.(元字符) .查找单个字符(除换行和行结束符) \w查找单词字符 \W查找非单词字符 \d查找数字 \D查找非数字字符 \s查找空白字符 \S查找非空白字符 \b匹配单词边界 \B匹配非单词边界<br>
        3.(方括号) [abc]查找方括号之间的任何字符  [^abc]查找任何不在方括号之间的字符  [0-9]查找任何从0至9的数字 [a-z]	查找任何从小写 a 到小写 z 的字符
        [A-Z]查找任何从大写 A 到大写 Z 的字符  [A-z]查找任何从大写 A 到小写 z 的字符  (red|blue|green)查找任何指定的选项<br>
        4.(量词修饰符):{n}表达式重复n次，比如：点击测试 "\w{2}" 相当于 "\w\w"；点击测试 "a{5}" 相当于 "aaaaa"<br>
        {m,n} 表达式至少重复m次，最多重复n次，比如：点击测试 "ba{1,3}"可以匹配 "ba"或"baa"或"baaa"<br>
        {m,}表达式至少重复m次，比如：点击测试 "\w\d{2,}"可以匹配 "a12","_456","M12344"...<br>
        ?匹配表达式0次或者1次，相当于 {0,1}，比如：点击测试 "a[cd]?"可以匹配 "a","ac","ad"<br>
        +表达式至少出现1次，相当于 {1,}，比如：点击测试 "a+b"可以匹配 "ab","aab","aaab"...<br>
        *表达式不出现或出现任意次，相当于 {0,}，比如：点击测试 "\^*b"可以匹配 "b","^^^b"...<br>
        4(杂项): n$	匹配任何结尾为 n 的字符串   ^n	匹配任何开头为 n 的字符串<br>
        ?=n	匹配任何其后紧接指定字符串 n 的字符串   ?!n	匹配任何其后没有紧接指定字符串 n 的字符串<br>
        | 左右两边表达式之间 "或" 关系，匹配左边或者右边<br>
        ( ) 在被修饰匹配次数的时候，括号中表达式可以作为整体被修饰 ;取匹配结果的时候，括号中的表达式匹配到的内容可以被单独得到<br>
        5(懒加载限定符):"*?"   重复任意次，但尽可能少重复<br>
        如 "acbacb"  正则  "a.*?b" 只会取到第一个"acb" 原本可以全部取到但加了限定符后，只会匹配尽可能少的字符 ，而"acbacb"最少字符的结果就是"acb"<br>
        "+?"  重复1次或更多次，但尽可能少重复<br>
        与上面一样，只是至少要重复1次<br>
        "??"  重复0次或1次，但尽可能少重复<br>
        如 "aaacb" 正则 "a.??b" 只会取到最后的三个字符"acb"<br>
        "{n,m}?"  重复n到m次，但尽可能少重复<br>
        如 "aaaaaaaa"  正则 "a{0,m}" 因为最少是0次所以取到结果为空<br>
        "{n,}?"    重复n次以上，但尽可能少重复<br>
        如 "aaaaaaa"  正则 "a{1,}" 最少是1次所以取到结果为 "a"<br>
        var pattern = new RegExp("\\w+");等价于var pattern = /\w+/;<br>
        str.search(pattern) 返回index  str.replace(pattern,"empty") str.exec(pattern) 匹配就返回该匹配值，否之 null<br>
        str.match(pattern) 匹配返回数组,否之null str.split(pattern) 拆分为数组
    </div>
</template>

<script>
    export default {
        name: "JSRegExp"
    }
</script>

<style lang="scss" scoped>

</style>



